import { Component, OnInit } from "@angular/core";
import { trigger, style, animate, transition } from "@angular/animations";
import { NbDialogRef, NbDialogService } from "@nebular/theme";
import { VideoService } from "../../../services/video.service";

@Component({
  selector: "app-video-listing",
  templateUrl: "./video-listing.component.html",
  styleUrls: ["./video-listing.component.css"],
  animations: [
    trigger("flyInOut", [
      transition("void => *", [
        style({ transform: "translateX(100%)" }),
        animate(400),
      ]),
    ]),
  ],
})
export class VideoListingComponent implements OnInit {
  videos: Array<any> = [];
  dialogRef: NbDialogRef<any>;
  videoUrl: string = "";

  constructor(
    private dialogService: NbDialogService,
    private videoService: VideoService
  ) {}

  ngOnInit() {
    this.getVideos();
  }

  getVideos() {
    this.videoService.getVideos().subscribe(
      (res: any) => {
        this.videos = res.data;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  // openVideoModal(template: any, videoDetails: any) {
  //   this.videoUrl = videoDetails.url;
  //   this.dialogRef = this.dialogService.open(template, {
  //     hasScroll: true,
  //     dialogClass: "model-full",
  //   });
  // }

  openVideoModal(template: any) {
    this.videoUrl = "http://13.76.172.134:3300/assets/video/output.mp4";
    this.dialogRef = this.dialogService.open(template, {
      hasScroll: true,
      dialogClass: "model-full",
    });
  }

  playPause() {
    var myVideo: any = document.getElementById("play_video");
    if (myVideo.paused) {
      myVideo.play();
    } else {
      myVideo.pause();
    }
  }

  processVideo() {
    this.videoService.processVideo().subscribe(
      (res: any) => {
        console.log(res);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  closeModal() {
    this.dialogRef.close();
  }
}
